@import url('./__container/infotooltip__container.css');
@import url('./__title/infotooltip__title.css');

.infotooltip {
  width: 430px;
  height: 330px;
  background: #FFFFFF;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  display: flex;
}

@media screen and (max-width : 650px) {
  .infotooltip {
    width: 255px;
    height: 308px;
    margin-left: 25px;
  }
}
